import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"


  const SecondPage = ({pageContext}) => (
    <>
  <Layout footer={true}>

    <div>
    <div style={{display:`flex`,flexWrap:`wrap`}} class="productContainer">
    <div class="photosPorduct">
      <div style={{position:`relative`,height:`50vh`}}>
      <img id="imgClickAndChange" src={pageContext.item.urlImg[0].img} style={{maxHeight:`100%`,maxWidth:`100%`,top:`50%`,left:`50%`,transform:`translate(-50%, -50%)`,margin:`0`,position:`absolute`}} alt="Avatar"></img>
      </div>
      <div style={{left: `0%`, top: '0%',position:`relative`,marginTop:'2vh', display:`flex`, justifyContent: 'flex-start'}}>
          {pageContext.item.urlImg.map((item, key) => {
              return(<div key={key} className="hoverElement" class="carrusselPhotosProduct" onClick = {() => document.getElementById("imgClickAndChange").src = item.img} onKeyDown={() => document.getElementById("imgClickAndChange").src = item.img} tabIndex={0} role = "button">
                <img src={item.img} style={{maxHeight:`100%`,maxWidth:`100%`,top:`50%`,left:`50%`,transform:`translate(-50%, -50%)`,margin:`0`,position:`absolute`,cursor:`pointer`}} alt="Avatar"></img>
              </div>)
          })}
      </div>
    </div>
    <div class="descriptionProduct">
      <div style={{position:`relative`,borderColor:`white`,borderStyle:`solid`}} >
      <h1 class="productDescriptionTitle">{pageContext.item.title}</h1>
      <p class="productDescriptionText">{pageContext.item.description}</p>
      
      <a href={pageContext.item.productUrl}>
      <button className="button" style={{borderRadius:`10%`}} >
        Miralo en Amazon!
      </button>
      </a>
      </div>
    </div>
    </div>
    </div>
  </Layout>
  </>
  )
  
        

export default SecondPage